const months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];
const rusMonths = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];
export const translateDate = (date: string) => {
  return (
    rusMonths[months.findIndex((m) => date.includes(m))] + " " + date.slice(-4)
  );
};
